<template>
  <div class="bg">
  <b-container fluid style="padding-top:1vh">
    <b-row>
      <b-col>
        <b-card class="opacity-75 scrollbar">
          <b-card-title>
            Ingredients You Have <b-button variant="danger" v-on:click="removeAll()">Remove All</b-button>
          </b-card-title>
          <b-card-text v-for="(item, i) in useritems" v-bind:key="i">
            {{toTitleCase(item)}} <b-icon style="cursor: pointer; right: 10px; float: right" v-on:click="remove(i)" icon="x" variant="danger" class="float-right"></b-icon>
          </b-card-text>
        </b-card>
      </b-col>
      <b-col>
        <input placeholder="Type ingredients in your inventory..." class="opacity-75" type="text" v-model="input" @keyup.enter="add()">
      </b-col>
      <b-col>
        <b-card title="Potential Available Recipes" class="opacity-75 scrollbar">
          <b-card-text v-for="(recipe, i) in allowed" :key="i">{{toTitleCase(recipe.item)}}</b-card-text>
          <b-card-footer v-if="allowed.length > 0">
            <b-badge variant="danger" class="text-black">NOTICE</b-badge>
            You may need multiple of some of these ingredients. Please refer to any reputable cookbook for the recipe and to make sure you have the right amount. We recommend "Cooking & Crafting" by Georgia Banks. Send a telegram to Georgia Banks, Raven McCoy, or Finley McCoy to purchase it for $25.
          </b-card-footer>
        </b-card>
      </b-col>
    </b-row>
    <div style="bottom:1vh" class="footer">
      Created and maintained by Indecision#7334
    </div>
  </b-container>
  </div>
</template>

<script>

export default {
  name: 'App',
  data () {
    return {
      input: '',
      useritems: [],
      recipes: [
        {
          item: 'bacon',
          ingredients: ['sliced pork']
        },
        {
          item: 'scrambled eggs',
          ingredients: ['egg', 'salt']
        },
        {
          item: 'coffee',
          ingredients: ['coffee grinds', 'water']
        },
        {
          item: 'tea',
          ingredients: ['tea leaves', 'water']
        },
        {
          item: 'bread',
          ingredients: ['grain', 'water']
        },
        {
          item: 'fried apples',
          ingredients: ['apple', 'bacon', 'butter']
        },
        {
          item: 'toast',
          ingredients: ['bread', 'butter']
        },
        {
          item: 'meat stew',
          ingredients: ['beef', 'potato', 'carrot', 'flour', 'animal fat', 'salt', 'water']
        },
        {
          item: 'meat stew',
          ingredients: ['lamb', 'potato', 'carrot', 'flour', 'animal fat', 'salt', 'water']
        },
        {
          item: 'succatash',
          ingredients: ['kidney beans', 'canned sweetcorn', 'salt', 'thyme']
        },
        {
          item: 'baked fish',
          ingredients: ['fish', 'egg', 'salt', 'english mace', 'animal fat', 'flour']
        },
        {
          item: 'turkey dinner',
          ingredients: ['plump bird', 'salt', 'thyme', 'bread', 'carrot', 'potato', 'animal fat', 'water']
        },
        {
          item: 'tavern biscuit',
          ingredients: ['sugar', 'brandy', 'butter', 'vanilla flour', 'flour']
        },
        {
          item: 'jerky',
          ingredients: ['salt', 'small sized meat']
        },
        {
          item: 'jerky',
          ingredients: ['salt', 'medium sized meat']
        },
        {
          item: 'fry bread',
          ingredients: ['flour', 'salt', 'sugar', 'animal fat']
        },
        {
          item: 'corn bread',
          ingredients: ['flour', 'butter', 'corn', 'salt', 'sugar']
        },
        {
          item: 'hardtack',
          ingredients: ['water', 'flour', 'salt']
        },
        {
          item: 'election cake',
          ingredients: ['egg', 'sugar', 'flour', 'brandy']
        },
        {
          item: 'christmas pudding',
          ingredients: ['bread', 'flour', 'butter', 'sugar', 'black currant', 'golden currant', 'rum', 'english mace']
        },
        {
          item: 'ginger bread',
          ingredients: ['sugar', 'butter', 'egg', 'flour', 'english mace']
        },
        {
          item: 'sarsparilla',
          ingredients: ['water', 'sugar', 'vailla flower', 'burdock root']
        },
        {
          item: 'candy cane',
          ingredients: ['sugar', 'corn', 'wild mint']
        },
        {
          item: 'cider cake',
          ingredients: ['sarsaparilla', 'butter', 'flour', 'sugar']
        },
        {
          item: 'tequila',
          ingredients: ['water', 'grain alcohol', 'wolf heart', 'sugar']
        },
        {
          item: 'beer',
          ingredients: ['grain', 'water']
        },
        {
          item: 'whiskey',
          ingredients: ['water', 'sugar', 'indian tobacco']
        },
        {
          item: 'brandy',
          ingredients: ['water', 'sugar', 'apples', 'raspberry']
        },
        {
          item: 'old fashioned',
          ingredients: ['whiskey', 'sugar', 'canned pineapple']
        },
        {
          item: 'mint julep',
          ingredients: ['wild mint', 'sugar', 'bourbon']
        },
        {
          item: 'sanguaro wine',
          ingredients: ['water', 'apple', 'sugar']
        },
        {
          item: 'mulled wine',
          ingredients: ['a class of sanguaro', 'brandy', 'water', 'sugar', 'english mace']
        },
        {
          item: 'racoon rum',
          ingredients: ['water', 'grain alcohol', 'sugar']
        },
      ],
      allowed: []
    }
  },
  created() {
    this.useritems = JSON.parse(localStorage.getItem('items')) || []
    this.getRecipes()
  },
  watch: {
    useritems: function(val) {
      localStorage.setItem('items', JSON.stringify(val))
      this.getRecipes();
    }
  },
  methods: {
    removeAll() {
      this.useritems = []
    },
    remove(i) {
      this.useritems.splice(i, 1)
      this.getRecipes();
    },
    add() {
      if (this.input.length > 0) {
        this.useritems.push(this.input.toLowerCase())
        this.getRecipes()
        this.input = ''
      }
    },
    getRecipes() {
      this.allowed = []
      for (let recipe of this.recipes) {
        let allowed = true
        for (let ingredient of recipe.ingredients) {
          if (this.useritems.indexOf(ingredient) === -1) {
            allowed = false
          }
        }
        if (allowed) {
          this.allowed.push(recipe)
        }

      }
    },
    toTitleCase(str) {
      return str.toLowerCase().split(' ').map(function (word) {
        return (word.charAt(0).toUpperCase() + word.slice(1));
      }).join(' ');
    }
  }
}
</script>

